.dialogs {
    display: grid;
    grid-template-rows: 170px 620px;

}

.dialog_profile {
    display: grid;
    grid-template-rows: 100px 70px;
}

.dialog_profile1 {
    display: grid;
    grid-template-columns: repeat(2, auto);
}

.dialog_profile img {
    width: 70px;
    border-radius: 15px;
    margin-top: 15px;
    margin-left: 15px;
}

.dialog_profile_text {
    margin-left: 0px;
    margin-top: 20px;
}

.dialog_search {
    cursor: pointer;
@media (max-width: 120 px) display: block;
    background: transparent;
    border: none;
    font-size: 20px;

}

.dialog_search1 {
    width: 170px;
    height: 30px;
    border-radius: 10px;
    border: 1px solid;
    color: rgba(0, 0, 0, 0.12);
    background-color: white;
    margin: 5px 8px 5px 5px;

}

h5 {
    font-family: 'Poppins', sans-serif;
    font-size: 1.200em;
}

.dialogs1 img {
    width: 50px;
    border-radius: 10px;
    margin: 10px;

}

.dialogs1 h5 {
    margin: 15px 50px 5px 5px;
}

.dialog1 {
    display: grid;
    grid-template-columns: repeat(3, auto);
    align-items: center;
    text-decoration: none;
    color: rgba(76, 79, 73, 0.88);
    padding: 5px;
}

.public {
    overflow-y: scroll;
    overflow-x: hidden;
    height: 450px;
}

.dialog_p {
    margin-top: 5px;
    font-size: 12px;
}

.Team_time {
    font-size: 10px;
    margin: 10px 10px 10px 15px;
}

.active {
    text-align: center;
    background-color: rgba(0, 194, 216, 0.67);
    border-radius: 50%;
    height: 18px;
    width: 18px;
    text-decoration: none;
    color: white;

    margin: 5px;

}

.time2 {
    margin: 5px;
    text-align: center;
}

h6 {
    font-size: 14px;
    line-height: 1.5;
    margin: 0;
    font-family: 'Poppins', sans-serif;
    font-weight: 400;
}

.dialog_border {
    border-bottom: 1px solid;
    border-color: rgba(0, 0, 0, 0.18);
    padding-bottom: 0;
}


