
.dialog_profile {
    display: grid;
    grid-template-rows: 100px 70px;
}

.dialog_profile1 {
    display: grid;
    grid-template-columns: repeat(2, auto);
}

.dialog_profile img {
    width: 70px;
    border-radius: 15px;
    margin-top: 15px;
    margin-left: 15px;
}

.dialog_profile_text {
    margin-left: 0px;
    margin-top: 20px;
}

.dialog_search {
    cursor: pointer;
@media (max-width: 120 px) display: block;
    background: transparent;
    border: none;
    font-size: 20px;

}

.dialog_search1 {
    width: 90%;
    height: 40px;
    border-radius: 10px;
    border: 1px solid;
    color: rgba(0, 0, 0, 0.12);
    background-color: white;


}

h5 {
    font-family: 'Poppins', sans-serif;
    font-size: 1.200em;
}